function Home() {
    const classes = useStyles();
    const [expanded, setExpanded] = useState(false);
    const [menuIsOpen, setMenuIsOpen] = useState(false);

    const prevRef = useRef(null);
    const nextRef = useRef(null);

    const history = useHistory();

    useEffect(() => {
        Aos.init({ duration: 1500 });
    }, [])

    return (
        <>
            <Header>
                <Logo src={LogoECNPBranca} />
                <Hamburger style={{ position: 'absolute', fontSize: '1.8rem', right: '4rem', zIndex: '1500', }} onClick={() => setMenuIsOpen(!menuIsOpen)} isOpen={menuIsOpen} />
                <Drawer
                    open={menuIsOpen}
                    anchor="right"
                    className={classes.drawer}
                    onClose={() => setMenuIsOpen(false)}
                    classes={{
                      paper: classes.drawerPaper,
                    }}
                >
                    <div className={classes.drawerPaper}>
                        <MenuDrawer style={{ height: '100%' }}>
                            <AnchorLink href="#home" onClick={() => setMenuIsOpen(false)} className={classes.anchorLink}>
                                <Button className={classes.anchorMenu}>
                                    Início
                                </Button>
                            </AnchorLink>
                            <AnchorLink href="#content" onClick={() => setMenuIsOpen(false)} offset='20' className={classes.anchorLink}>
                                <Button className={classes.anchorMenu}>
                                    Conteúdo
                                </Button>
                            </AnchorLink>
                            <AnchorLink href="#vantagens" onClick={() => setMenuIsOpen(false)} offset='100' className={classes.anchorLink}>
                                <Button className={classes.anchorMenu}>
                                    Vantagens
                                </Button>
                            </AnchorLink>
                            <AnchorLink href="#depoiments" onClick={() => setMenuIsOpen(false)} offset='100' className={classes.anchorLink}>
                                <Button className={classes.anchorMenu}>
                                    Depoimentos
                                </Button>
                            </AnchorLink>
                            <AnchorLink href="#inscrition" onClick={() => setMenuIsOpen(false)} className={classes.anchorLink}>
                                <Button className={classes.buttonHeader}>
                                    Inscrição
                                </Button>
                            </AnchorLink>
                        </MenuDrawer>
                    </div>
                </Drawer>
                <MenuHeader>
                    <AnchorLink href="#home" className={classes.anchorLink}>
                        <Button className={classes.anchorMenu}>
                            Início
                        </Button>
                    </AnchorLink>
                    <AnchorLink href="#content" offset='20' className={classes.anchorLink}>
                        <Button className={classes.anchorMenu}>
                            Conteúdo
                        </Button>
                    </AnchorLink>
                    <AnchorLink href="#vantagens" offset='100' className={classes.anchorLink}>
                        <Button className={classes.anchorMenu}>
                            Vantagens
                        </Button>
                    </AnchorLink>
                    <AnchorLink href="#depoiments" offset='100' className={classes.anchorLink}>
                        <Button className={classes.anchorMenu}>
                            Depoimentos
                        </Button>
                    </AnchorLink>
                    <AnchorLink href="#inscrition" className={classes.anchorLink}>
                        <Button className={classes.buttonHeader}>
                            Inscrição
                        </Button>
                    </AnchorLink>
                </MenuHeader>
            </Header>
            <Container>
                <BoxInitial id="home" imgPulpit={Pulpit}>
                    <Box>
                        <BoxLogo>
                            <LogoInitial data-aos="fade-up" data-aos-delay='200' src={LogoECNP} />
                        </BoxLogo>
                        <Typography data-aos="fade-up" className={classes.subTitle}>
                            O curso
                        </Typography>
                        <Typography data-aos="fade-up" className={classes.titleInitial}>
                            O que é o ECNP?
                        </Typography>
                        <Typography data-aos="fade-up" data-aos-delay='200' className={classes.descriptionInitial}>
                            Neste curso iremos da preparação à entrega do sermão, considerando
                            passo a passo cada etapa importante para a composição de pregações bíblicas e relevantes para o contexto atual. 
                            Parafraseando John Stott, pregar é construir pontes entre o mundo bíblico e o mundo contemporâneo,
                            respondendo as perguntas de hoje com as respostas bíblicas.
                        </Typography>
                        <BoxLogo>
                            <AnchorLink href="#inscrition" className={classes.anchorLink}>
                                <Button data-aos="fade-up" data-aos-delay='200' className={classes.button}>
                                    Inscrever-se Agora
                                </Button>
                            </AnchorLink>
                        </BoxLogo>
                    </Box>
                </BoxInitial>
                <ImageBox id="content">
                    <Image data-aos="fade-up" src={Image1} />
                    <Texts>
                        <Typography data-aos="fade-up" data-aos-delay='200' className={classes.subTitle}>
                            Áreas de Estudo
                        </Typography>
                        <Typography data-aos="fade-up" data-aos-delay='200' className={classes.title}>
                            Teologia Bíblica da Pregação
                        </Typography>
                        <Typography data-aos="fade-up" data-aos-delay='200' className={classes.description}>
                        O modo como enxergamos as Escrituras determina a maneira como a proclamamos. Tanto a leitura, como a interpretação e a pregação precisam ser feitas no temor do Senhor. O Criador de tudo se acomodou aos pecadores e falou, e ainda fala, por meio de um livro e por meio da pregação. Quando a Bíblia é aberta e pregada fielmente, pregador e auditório têm um encontro com o Deus vivo que se comunica por meio da proclamação do evangelho.
                        </Typography>
                    </Texts>
                </ImageBox>
                <ImageBox>
                    <Texts>
                        <Typography data-aos="fade-up" data-aos-delay='200' className={classes.subTitle}>
                            Componentes Básicos
                        </Typography>
                        <Typography data-aos="fade-up" data-aos-delay='200' className={classes.title}>
                            Princípios de Interpretação e Exposição Bíblica
                        </Typography>
                        <Typography data-aos="fade-up" data-aos-delay='200' className={classes.description}>
                        A boa pregação expositiva toma seu conteúdo das Escrituras, desvendando o significado original pretendido por Deus no texto e aplicando os princípios bíblicos encontrados àqueles que hoje buscam sua orientação. Expor a Bíblia é ler, interpretar e aplicar os pensamentos de Deus revelados por Ele de um modo fiel. O que servia para a época bíblica, é aplicado fiel, poderosa, direta e pessoalmente aos dilemas do homem moderno.
                        </Typography>
                    </Texts>
                    <Image data-aos="fade-up" src={Image2} />
                </ImageBox>
                <BoxContent>
                    <BoxRow>
                        <Flex1>
                            <Typography data-aos="fade-up" data-aos-delay='200' className={classes.subTitle}>
                                Oque terá?
                            </Typography>
                            <Typography data-aos="fade-up" data-aos-delay='200' className={classes.title}>
                                Vantagens do Curso
                            </Typography>
                            <Ul>
                                <ListItem data-aos="fade-up" data-aos-delay='200' itemCheck={ItemCheck}>
                                    <BgCheck>
                                        <CheckIcon style={{ color: '#00B4DB' }} className={classes.checkItem} />
                                    </BgCheck>
                                    <TextListItem>
                                        <Typography className={classes.descriptionStrong}>
                                            Tempo
                                        </Typography>
                                        <Typography className={classes.description}>
                                            Tempo dedicado para pensar o ministério público da pregação no contexto das Sagradas Escrituras.
                                        </Typography>
                                    </TextListItem>
                                </ListItem>
                                <ListItem data-aos="fade-up" data-aos-delay='200' itemCheck={ItemCheck}>
                                    <BgCheck>
                                        <CheckIcon style={{ color: '#00B4DB' }} className={classes.checkItem} />
                                    </BgCheck>
                                    <TextListItem>
                                        <Typography className={classes.descriptionStrong}>
                                            Conceito
                                        </Typography>
                                        <Typography className={classes.description}>
                                            Estudo de vários conceitos do que vem a ser um sermão bíblico centrado em Cristo e no evangelho, seja no Antigo ou no Novo Testamento
                                        </Typography>
                                    </TextListItem>
                                </ListItem>
                                <ListItem data-aos="fade-up" data-aos-delay='200' itemCheck={ItemCheck}>
                                    <BgCheck>
                                        <CheckIcon style={{ color: '#00B4DB' }} className={classes.checkItem} />
                                    </BgCheck>
                                    <TextListItem>
                                        <Typography className={classes.descriptionStrong}>
                                            Ferramentas
                                        </Typography>
                                        <Typography className={classes.description}>
                                            Conhecimento das ferramentas básicas para a boa leitura, observação e interpretação de um texto bíblico, respeitando as regras da gramática da língua portuguesa e a instrumentalidade das línguas bíblicas
                                        </Typography>
                                    </TextListItem>
                                </ListItem>
                                <ListItem data-aos="fade-up" data-aos-delay='200' itemCheck={ItemCheck}>
                                    <BgCheck>
                                        <CheckIcon style={{ color: '#00B4DB' }} className={classes.checkItem} />
                                    </BgCheck>
                                    <TextListItem>
                                        <Typography className={classes.descriptionStrong}>
                                            Processos
                                        </Typography>
                                        <Typography className={classes.description}>
                                            Assuntos como métodos de estudos bíblicos, processos de preparação de sermões bíblicos serão tratados em nosso treinamento
                                        </Typography>
                                    </TextListItem>
                                </ListItem>
                                <ListItem data-aos="fade-up" data-aos-delay='200' itemCheck={ItemCheck}>
                                    <BgCheck>
                                        <CheckIcon style={{ color: '#00B4DB' }} className={classes.checkItem} />
                                    </BgCheck>
                                    <TextListItem>
                                        <Typography className={classes.descriptionStrong}>
                                            Familiarização
                                        </Typography>
                                        <Typography className={classes.description}>
                                            Familiarização com os passos práticos necessários à boa e eficaz pregação bíblica
                                        </Typography>
                                    </TextListItem>
                                </ListItem>
                            </Ul>
                        </Flex1>
                        {/* <Accordion onMouseEnter={() => setExpanded2(true)} expanded={expanded2} onMouseLeave={() => setExpanded2(false)} className={classes.content}>
                            <AccordionSummary expandIcon={<ExpandMoreIcon className={classes.icon} style={{ color: 'white' }} />} className={classes.summary}>
                                    Por que fazer o curso?
                            </AccordionSummary>
                            <AccordionDetails className={classes.detailsBottom}>
                            Como o curso visa auxiliar pastores, professores, pregadores e estudantes de Bíblia, experientes e iniciantes, capacitando-os e ajudando-os ainda mais em seus estudos e pregações, para que possam desenvolver e distribuir sermões fielmente baseados na Bíblia, de um modo claro e aplicado aos dias atuais, penso que tanto o aluno, bem como seu auditório (público) serão muito beneficiados. Num mundo de tantas vozes, as pessoas nas igrejas e fora delas estão famintas pela boa pregação da Palavra de Deus. E, diante de tantas distrações e correrias do dia a dia, os pregadores e professores bíblicos precisam de auxílio, boas dicas e caminhos para se prepararem melhor, com mais objetividade, para o ministério da pregação.
                            E, se os pastores, professores e pregadores do evangelho, são os responsáveis por comunicar Cristo e ministrar a Palavra de Deus em todos os momentos, de muitas maneiras e a todas as pessoas, sendo a ministração da Palavra de Deus é vital, temos que nos reciclar para que nossos sermões sejam mais bíblicos, mais sólidos, de modo que se conectem com nosso público de maneira significativa e transformadora. Logo, se você é um estudante iniciante em pregação ou um veterano que procura aprimorar suas habilidades de pregação, o curso de Teologia da Pregando e Exposição Bíblica do Projeto Eu Creio na Pregação (ECNP) é uma valiosa oportunidade para você.
                            </AccordionDetails>
                        </Accordion> */}
                        <BoxRowMsm>
                            <Flex3 id="vantagens" data-aos="fade-up" data-aos-delay='200'>
                                <People1 className="people1" src={imgPeople1} />
                                <People2 className="people2" src={imgPeople2} />
                            </Flex3>
                            <Flex4 data-aos="fade-up" data-aos-delay='200'>
                                <People3 className="people1" src={imgPeople3} />
                                <People4 className="people2" src={imgPeople4} />
                                <People5 className="people1" src={imgPeople5} />
                                <People6 className="people2" src={imgPeople6} />
                            </Flex4>
                        </BoxRowMsm>
                    </BoxRow>
                    <CardBecause data-aos="fade-up">
                        <TextListItem>
                            <Typography data-aos="fade-up" data-aos-delay='200' className={classes.subTitle}>
                                Oque terá?
                            </Typography>
                            <Typography data-aos="fade-up" data-aos-delay='200' className={classes.title}>
                                Porque fazer o curso?
                            </Typography>
                        </TextListItem>
                        <TextListItem data-aos="fade-up" data-aos-delay='200'>
                            <Typography className={classes.descriptionStrong}>
                            O curso visa auxiliar a todos os estudantes de Bíblia, experientes e iniciantes, em seus estudos e pregações, para que possam desenvolver e distribuir sermões fielmente baseados na Bíblia, de um modo claro e aplicado aos dias atuais.
                            </Typography>
                            <Typography className={classes.descriptionCardBecause}>
                                Tanto o aluno, bem como seu auditório (público) serão muito beneficiados pelo curso. Num mundo de tantas vozes, as pessoas estão famintas pela boa pregação da Palavra. E, diante de tantas distrações, arautos da Palavra precisam de auxílio, boas dicas e caminhos para se prepararem melhor, com mais objetividade, para o ministério da pregação. Se os pregadores são os responsáveis por comunicar Cristo em todos os momentos, de muitas maneiras e a todas as pessoas, é urgente que tais discípulos se reciclem para que seus sermões sejam mais bíblicos, relevantes e transformadores. O curso é útil para quem procura aprimorar suas habilidades de leitura bíblica, interpretação e composição de sermões, bem como o ato da pregação.
                            </Typography>
                        </TextListItem>
                    </CardBecause>
                </BoxContent>
                <BoxDepoiment id="depoiments">
                    <Title>
                        <Typography data-aos="fade-up" data-aos-delay='200' className={classes.subTitle}>
                            Depoimentos
                        </Typography>
                        <Typography data-aos="fade-up" data-aos-delay='200' className={classes.title}>
                            Alunos do ECNP
                        </Typography>
                        <DepoimentControllers data-aos="fade-up" data-aos-delay="200">
                            <Expand onClick={() => setExpanded(!expanded)}>
                                Clique aqui para expandir
                                <Typography className={classes.subZinho}>Arraste para o lado para ver mais depoimentos</Typography>
                            </Expand>
                            <Controllers>
                                <ArrowBackIosIcon ref={prevRef} className={classes.iconController} />
                                <ArrowForwardIosIcon ref={nextRef} className={classes.iconController} />
                            </Controllers>
                        </DepoimentControllers>
                    </Title>
                    <Swiper
                        spaceBetween={0}
                        onInit={(swiper) => {
                            swiper.params.navigation.prevEl = prevRef.current;
                            swiper.params.navigation.nextEl = nextRef.current;
                            swiper.navigation.init();
                            swiper.navigation.update();
                        }}
                        slidesPerView={window.innerWidth < 769 ? 1 : 3}
                        onSlideChange={() => console.log('slide change')}
                        onSwiper={(swiper) => console.log(swiper)}
                        style={{ width: '100%', cursor: 'pointer', }}
                        data-aos="fade-up" data-aos-delay='200'
                    >
                        <SwiperSlide>
                            <CardSwiper expanded={expanded}>
                                <Typography className={classes.titleDepoiments}>
                                    Enezir Velasco de Oliveira
                                </Typography>
                                <Typography className={classes.descriptionDepoiments}>
                                    O curso me ajudou a compreender a necessidade de conhecer, com profundidade, o conteúdo bíblico, como me inteirar da situação vivida na época, pelos homens que a escreveram, e ainda por aqueles que vivenciaram e testemunharam os feitos de Jesus. Além disso, a importância de transmitirmos com fidelidade
                                    , hoje, a verdadeira história bíblica, de Genesis à Apocalipse: 'o homem foi criado para... glorificar o Criador. Com a queda, anunciar a vinda de Jesus para resgatar o homem à voltar-se para o Criador'. Em Resumo, essa é a verdadeira mensagem a ser pregada. Isso é pregar o verdadeiro evangelho. Eu recomendo o curso 'Eu Creio na Pregação', porque é apresentado ferramentas necessárias para um melhor entendimento e aprofundamento;  abre novos horizontes; é claro; temos a liberdade de fazer perguntas e sanar dúvidas; as aulas  são gravadas, recebemos os links, e no final ficamos com um vasto material para consulta sempre que necessário; além dos exemplos através de videos e pregações que nos ajudam na compreensão do que está sendo ensinado. Para mim trouxe, também, edificação espiritual.
                                </Typography>
                                <Typography className={classes.titleDepoiments}>
                                    ...
                                </Typography>
                            </CardSwiper>
                        </SwiperSlide>
                        <SwiperSlide>
                            <CardSwiper expanded={expanded}>
                                <Typography className={classes.titleDepoiments}>
                                Wolney Miranda
                                </Typography>
                                <Typography className={classes.descriptionDepoiments}>
                                É um curso bastante relevante. Me ajudou a entender e a desenvolver,  principalmente, a Pregação Expositiva. Recomendo. Porque é um curso sério, preocupado em ensinar  os alunos em uma cosmovisão bíblica e atualizada. O valor do curso é acessível a todos que interessar, o estudo aplicado é de fácil entendimento
                                , por mais que foi on-line, não apresentou nenhuma dificuldade de entendimento e interação, professor/pastor é conhecedor do assunto e tem muito manejo em ensinar, tornando as aulas prazerosas e compreensíveis e por fim, para aqueles que querem aprender para ensinar, não perca tempo. Inscrevam-se e aproveitem o máximo que puder.
                                </Typography>
                                <Typography className={classes.titleDepoiments}>
                                    ...
                                </Typography>
                            </CardSwiper>
                        </SwiperSlide>
                        <SwiperSlide>
                            <CardSwiper expanded={expanded}>
                                <Typography className={classes.titleDepoiments}>
                                Bruno Urubatan Rangel Da Silva
                                </Typography>
                                <Typography className={classes.descriptionDepoiments}>
                                O curso eu creio na pregação mudou a minha maneira de ler as escrituras e de montar o meu sermão, hoje tenho uma visão mais aprofundada e consigo ser mais fiel as escrituras Eu recomendo o curso, pois, a palavra de Deus é verdadeira
                                 e o anunciador desse evangelho precisa saber o que está pregando, e aí fazer o curso você terá um divisor de águas e será mais eficiente na pregação.
                                </Typography>
                                <Typography className={classes.titleDepoiments}>
                                    ...
                                </Typography>
                            </CardSwiper>
                        </SwiperSlide>
                        <SwiperSlide>
                            <CardSwiper expanded={expanded}>
                                <Typography className={classes.titleDepoiments}>
                                Valéria Passos
                                </Typography>
                                <Typography className={classes.descriptionDepoiments}>
                                Foi útil pra mim no sentido de me ensinar a organizar um texto bíblico e apontar a ideia central do texto. Por que neste curso a pessoa irá aprender as formas básicas de organização de um texto e vai aprimorar seus conhecimentos.
                                </Typography>
                            </CardSwiper>
                        </SwiperSlide>
                        <SwiperSlide>
                            <CardSwiper expanded={expanded}>
                                <Typography className={classes.titleDepoiments}>
                                Célia Aparecida Alvarez Cota
                                </Typography>
                                <Typography className={classes.descriptionDepoiments}>
                                O Curso ECNP foi um grande desafio, impactante, e tremendamente didático e contemporâneo aprendi muito sempre que possível revejo o material. Recomendo o curso, tanto que 2 irmãos já estão matriculados para serem poderosamente abençoados A razão é que este curso além de ser extremamente esclarecedor
                                 será um divisor de águas àqueles que se dispuserem para tanto. Louvo a Deus pela vida do Pr. Luis Filipe.
                                </Typography>
                                <Typography className={classes.titleDepoiments}>
                                    ...
                                </Typography>
                            </CardSwiper>
                        </SwiperSlide>
                        <SwiperSlide>
                            <CardSwiper expanded={expanded}>
                                <Typography className={classes.titleDepoiments}>
                                Patrícia Vianna
                                </Typography>
                                <Typography className={classes.descriptionDepoiments}>
                                Muito útil para montar uma pregação sem sair da verdade bíblica.  Esclarecedor e muito diferente do que aprendi no seminário. Recomendo para que mais pregadores proclamem a Palavra de Deus com segurança, verdade e dedicação.
                                </Typography>
                            </CardSwiper>
                        </SwiperSlide>
                        <SwiperSlide>
                            <CardSwiper expanded={expanded}>
                                <Typography className={classes.titleDepoiments}>
                                Marco Antônio Pires de Carvalho
                                </Typography>
                                <Typography className={classes.descriptionDepoiments}>
                                O curso Creio na Pregação caiu na minha vida pôr misericórdia do senhor, e pela grande irmã Enezir. O curso foi e está sendo  muito importante e útil, não só pôr ler a palavra do Senhor, mais também para entender e passar a diante
                                 o real sentindo do texto, dentro da palavra do Senhor, a Bíblia Sagrada. Não só recomendo, como já passei para vários amigos e irmãos. A forma com que o Pr. Felipe coloca as aulas é muito interessante e prática. Quando paramos para analisar, surge o esclarecimento do Espírito Santo.
                                </Typography>
                                <Typography className={classes.titleDepoiments}>
                                    ...
                                </Typography>
                            </CardSwiper>
                        </SwiperSlide>
                        <SwiperSlide>
                            <CardSwiper expanded={expanded}>
                                <Typography className={classes.titleDepoiments}>
                                Pr. Antônio Paulo da Costa
                                </Typography>
                                <Typography className={classes.descriptionDepoiments}>
                                Foi útil para mim, porque despertou o que está no meu coração, o priorizar essa graça salvadora, coisa rara em nossos dias. Eu recomendo esse curso, porque através dele quem participar terá a noção de que quem prega a Palavra deve
                                 ter conhecimento Dela, e que nunca deve ser fundamentada em conceitos humanos.
                                </Typography>
                            </CardSwiper>
                        </SwiperSlide>
                        <SwiperSlide>
                            <CardSwiper expanded={expanded}>
                                <Typography className={classes.titleDepoiments}>
                                Ana Cláudia Marins
                                </Typography>
                                <Typography className={classes.descriptionDepoiments}>
                                Foi útil pelo conhecimento de técnicas e da organização do pensamento diante do texto bíblico. Foi como que cortinas abrindo me permitindo contemplar como a pregação requer muitos detalhes, cuidado, sensibilidade, temor e tremor.
                                 Pela responsabilidade em pregar a Palavra, penso que quem almeja ou vislumbra esse chamado em sua caminhada, não terá condições de faze-lo sem chamado e preparo. O chamado é com Deus. O preparo, nós providenciamos.
                                </Typography>
                                <Typography className={classes.titleDepoiments}>
                                    ...
                                </Typography>
                            </CardSwiper>
                        </SwiperSlide>
                    </Swiper>
                </BoxDepoiment>
                <BoxFinal id="inscrition" imgPr={PrImg}>
                    <Card data-aos="fade-right">
                        <Typography className={classes.subTitle}>
                            INVESTIMENTO
                        </Typography>
                        <Typography className={classes.titleCard}>
                            QUAL O INVESTIMENTO?
                        </Typography>
                        <Typography className={classes.valueCard}>
                            R$199,95 ou 5x R$39,99 
                        </Typography>
                        <Typography className={classes.contentCard}>
                            30 HORAS DE CURSO
                            <br/>36 AULAS DE 45MIN
                            <br/>2 VÍDEOS DE 45MIN
                            <br/>18 ENCONTROS ON-LINE + DUAS VÍDEO-AULAS EXTRAS
                            <br/>SUPORTE DE UM SEMESTRE LETIVO
                            <br/>MATERIAL DIDÁTICO
                            <br/>AULAS 100% ON-LINE
                            <br/>AO VIVO ÁS TERÇAS-FEIRAS
                        </Typography>
                        <Button className={classes.buttonPay} onClick={() => history.push('/inscrition')}>
                            INSCREVER-SE
                        </Button>
                        <Payments>
                            <ImagePayment src={LogoPix} />
                            <ImagePayment src={LogoBoleto} />
                        </Payments>
                    </Card>
                </BoxFinal>
            </Container>
        </>
    )
}

export default Home;